.mask {
    width: 100%;
    height: 100vh;
    position: relative;
}

.intro-img {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000000;
    opacity: 0.5;
}

.hero {
    height: 100%;
    width: 100%;
}

.hero .content{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
    color: white;
}

.hero .content h1{
    font-size: 4rem;
    padding: 0.1rem 0 1rem;
}

.hero .content p{
    font-size: 1.9rem;
    font-weight: 500;
    text-transform: uppercase;
}

.content .btn {
    margin: 1rem 0.2rem;
}

@media screen and (max-width:1000px) {
    .hero .content h1{
        font-size: 3rem;
    }
    
    .hero .content p{
        font-size: 1.4rem;
    }
}

@media screen and (max-width:768px) {
    .hero .content h1{
        font-size: 2.5rem;
        padding: 0.5rem 0 1rem;
    }
    
    .hero .content p{
        font-size: 1.2rem;
    }
    
    .content .btn {
        padding: 10px 20px;
        font-size: 0.9rem;
        margin: 0.5rem 0.2rem;
    }
}

@media screen and (max-width:480px) {
    .hero .content {
        width: 90%;
    }
    
    .hero .content h1{
        font-size: 2rem;
        padding: 0.3rem 0 0.8rem;
    }
    
    .hero .content p{
        font-size: 1rem;
    }
    
    .content .btn {
        padding: 8px 16px;
        font-size: 0.8rem;
        margin: 0.5rem 0.1rem;
        display: inline-block;
    }
    
    .mask {
        height: 100vh;
        min-height: 500px;
    }
}